'use strict'

import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App.vue'
import { routes } from './router'

//使用路由
Vue.use(VueRouter);

//new()
const router = new VueRouter({
    routes,
    mode: 'history',
    scrollBehavior(to, from, savePosition) {
        // return { x: 0, y: 100 };
        // return { selector: '.btn' };
        if (savePosition) {
            return savePosition;
        } else {
            return { x: 0, y: 0 };
        }
    }
});

//全局守卫
