//     if (to.path === '/login' || to.path === '/register') {
//         next();
//     } else {
//         alert('你还没有登录,请先登录.');
//         next('/login');
//     }
// })

//后置钩子
// router.afterEach((toolbar, from, next) => {
//     alert('你还没登录，请先登录');
// })

new Vue({
    router,
    el: '#app',
    render: h => h(App)
})


