<template>
    <h1>Admin</h1>
</template>

<script>
    export default {
        data() {
            return {
                name: 'Mike'
            }
        },
        beforeRouteEnter: (to, from, next) => {
            next(vm => {
                alert( 'hello' + vm.name);
            });
        }
    }
</script>

<style scoped>

</style>



